import React from 'react';
import { Table } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import Watched from './Watched';


function Cart(props) {

    let reduxState = useSelector((state) => state);
    console.log(reduxState);
    let dispatch = useDispatch();

    return (
        <div>
            <Table responsive="xl">
                <thead>
                    <tr>
                        <th>#</th>
                        <th>상품명</th>
                        <th>생산국</th>
                        <th>가격</th>
                        <th>수량</th>
                        <th>변경</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        reduxState.reducer.map((a, i) => {
                            return (
                                <tr key={i}>
                                    <td>{a.id}</td>
                                    <td>{a.title}</td>
                                    <td>{a.content}</td>
                                    <td>{a.price}</td>
                                    <td>{a.quan}</td>
                                    <td><button onClick={() => {
                                        dispatch({ type: '요청-수량증가', payload: a.id })
                                    }}>+</button>
                                        <button onClick={() => {
                                            dispatch({ type: '요청-수량감소', payload: a.id })
                                        }}>-</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
            {
                reduxState.reducer2 === true ?
                    (
                        <div className='my-alert'>
                            <p>지금 구매하시면 신규할인 20%</p>
                            <button onClick={() => {
                                dispatch({ type: 'close-alert' })
                            }}>닫기</button>
                        </div>
                    )
                    : null

            }
            <Watched __product={props._product}></Watched>

        </div >
    )
}
