export default Cart;

// 순서 정리: 커넥트 함수> 스토어 가져오는 함수 > 그 함수의 변수에 스토어가 들어옴 > 그 스토어를 변수 이름 정해서 가져옴 > 프롭스로 사용가능. 
// 컴포넌트마다 프롭스 변수 넘겨주는 일 안해도 프로바이더로 변수 다 내보내기 가능