import React, { useEffect, useState } from 'react';
import { Nav } from 'react-bootstrap';
import { connect } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import Watched from './Watched';

function Detail(props) {

    //구버전 리덕스 코드 사용.

    let [alertOn, setAlertOn] = useState(true);
    let [tab, setTab] = useState(1);
    //모달창. 현재창 보이는. 현재 상태 저장해둬야 함.
    useEffect(() => {
        let timer = setTimeout(() => { setAlertOn(false) }, 2000);
        return () => clearTimeout(timer)
    }, []);
    //시간이 지나 실행되는 기능. 마지막 ,[]추가하면 []안의 데이터가 변결될때만 실행되게 만드는 조건 기능. 빈칸시 언제나 단 한번만 수행됨.
    let navigate = useNavigate();
    let { id } = useParams();
    let pickProduct = props._product.find(function (x) {
        return x.id == id
        // 이 리턴에 의해 주소창에 입력한 파라미터 id 와 동일한 값인 id를 가진 상품이 pickProduct 에 담김.
    });
    console.log(pickProduct);
    useEffect(() => {
        let arr = localStorage.getItem('watched');
        if (arr === null) {
            arr = []
        }
        else {
            arr = JSON.parse(arr);
        }
        arr.unshift(id);
        arr = new Set(arr);
        arr = [...arr].slice(0, 4);
        localStorage.setItem('watched', JSON.stringify(arr));
    }, []);

    return (
        <>
            <div className="container">
                {
                    alertOn === true ?
                        (<div className="my-alert">
                            <p>오늘 새로 입고된 상품입니다</p>
                        </div>)
                        : null
                }
                <div className="row">
                    <div className="col-md-6">
                        <img src={pickProduct.thumbnail} width="100%" alt="thumbnail" />
                    </div>
                    <div className="col-md-6 mt-4">
                        <h4 className="pt-5">{pickProduct.title}</h4>
                        <p>{pickProduct.content}</p>
                        <p>{pickProduct.price}</p>
                        <Inventory __inventory={props._inventory}></Inventory>
                        <button className="btn btn-danger" onClick={() => {
                            props._setInventory([9, 11, 12]);
                            props.dispatch({ type: '요청-항목추가', payload: { id: pickProduct.id, title: pickProduct.title, content: pickProduct.content, price: pickProduct.price, quan: 1 } });
                            // navigate('/cart');
                        }}>주문하기</button>
                        <button className="btn btn-danger" onClick={() => {
                            navigate('/cart');
                        }}>장바구니 가기</button>
                    </div>
                </div>
            </div >

            <div className="container my-5">
                <Nav variant="tabs" defaultActiveKey="link1">
                    <Nav.Item className='tab'>
                        <Nav.Link eventKey="link1" onClick={() => {
                            setTab(1)
                        }}>Tab1</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className='tab'>
                        <Nav.Link eventKey="link2" onClick={() => {
                            setTab(2)
                        }}>Tab2</Nav.Link>
                    </Nav.Item>
                    <Nav.Item className='tab'>
                        <Nav.Link eventKey="link3" onClick={() => {
                            setTab(3)
                        }}>
                            Tab3
                        </Nav.Link>
                    </Nav.Item>
                </Nav>
                <Tabcontent _tab={tab} />
            </div>

            <Watched __product={props._product}></Watched>
        </>

    )
}

function Inventory(props) {
    return (
        <p>재고: {props.__inventory[0]}</p>
    )
}

function Tabcontent(props) {

    return (
        [<div>내용1</div>, <div>내용2</div>, <div>내용3</div>][props._tab - 1]
    )
}

function reduxImporter(reduxStore) {
    console.log(reduxStore);
    return {
        _reducerBasket: reduxStore.reducer,
        _reducerAlert: reduxStore.reducer2
    }
}
export default connect(reduxImporter)(Detail);
